<template>
    <div>
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#">首页</a>
                </li>

                <li>
                    <a>用户管理</a>
                </li>
            </ul>
            <!-- .breadcrumb -->
        </div>

        <div class="page-content">
            <div class="row">
                <div class="col-xs-12">
                    <!-- PAGE CONTENT BEGINS -->

                    <div class="row">
                        <div class="col-xs-12">
                            <div class="table-responsive">
                                <table id="sample-table-1" class="table table-striped table-bordered table-hover">
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>账号</th>
                                            <th>权限</th>
                                            <th>
                                                <i class="icon-time bigger-110 hidden-480"></i> 创建时间
                                            </th>
                                            <th></th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr v-for="(item, index) in list">
                                            <td>{{index+1}}</td>
                                            <td>
                                                <a>{{item.UserName}}</a>
                                            </td>
                                            <td class="hidden-480">
                                                <span v-if="item.Pemission==1" class="label label-sm label-inverse arrowed-in">管理员</span>
                                                <span v-if="item.Pemission==2" class="label label-sm label-warning">支部书记</span>
                                                <span v-if="item.Pemission==3" class="label label-sm label-success">普通用户</span>
                                            </td>
                                            <td class="hidden-480">{{item.CreateOn}}</td>

                                            <td>
                                                <div class="visible-md visible-lg hidden-sm hidden-xs btn-group">
                                                    <button class="btn btn-xs btn-info">
																	<i class="icon-edit bigger-120"></i>
																</button>

                                                    <button v-on:click="remove(item.UserName)" class="btn btn-xs btn-danger">
																	<i class="icon-trash bigger-120"></i>
																</button>

                                                    <button v-if="item.Pemission!=1" class="btn btn-xs btn-warning">
																	设置为管理员
																</button>
                                                    <button v-if="item.Pemission!=2" class="btn btn-xs btn-warning">
																	设置为支付书记
																</button>
                                                    <button v-if="item.Pemission!=3" class="btn btn-xs btn-warning">
																	设置为普通用户
																</button>
                                                </div>

                                                <div class="visible-xs visible-sm hidden-md hidden-lg">
                                                    <div class="inline position-relative">
                                                        <button class="btn btn-minier btn-primary dropdown-toggle" data-toggle="dropdown">
																		<i class="icon-cog icon-only bigger-110"></i>
																	</button>

                                                        <ul class="dropdown-menu dropdown-only-icon dropdown-yellow pull-right dropdown-caret dropdown-close">
                                                            <li>
                                                                <a href="#" class="tooltip-info" data-rel="tooltip" title="View">
                                                                    <span class="blue">
																					<i class="icon-zoom-in bigger-120"></i>
																				</span>
                                                                </a>
                                                            </li>

                                                            <li>
                                                                <a href="#" class="tooltip-success" data-rel="tooltip" title="Edit">
                                                                    <span class="green">
																					<i class="icon-edit bigger-120"></i>
																				</span>
                                                                </a>
                                                            </li>

                                                            <li>
                                                                <a href="javascript:void(0)" class="tooltip-error" data-rel="tooltip" title="Delete">
                                                                    <span class="red">
																					<i class="icon-trash bigger-120"></i>
																				</span>
                                                                </a>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <!-- /.table-responsive -->
                        </div>
                        <!-- /span -->
                    </div>
                    <!-- /row -->

                </div>
                <!-- /.col -->
            </div>
            <!-- /.row -->
        </div>
    </div>
</template>

<script>
    export default {
        name: 'vUserslist',
        data() {
            return {
                list: []
            }
        },
        created: function () {
            var self = this;

            fetch('http://dev.school.com/user/list')
                .then(response => response.json())
                .then(data => self.list = data)
        },
        methods: {
            remove(userName) {
                if (confirm("确认删除" + userName + "吗?")) {
                    var self = this;

                    fetch('http://dev.school.com/user/delete?userName=' + userName)
                        .then(response => response.json())
                        .then(() => {
                            fetch('http://dev.school.com/user/list')
                                .then(response => response.json())
                                .then(data => self.list = data)
                        })
                }
                else {
                    return;
                }

            }
        }
    }
</script>


